
import Timer from '../Pages/Timer';
import { Intensity } from '../Logic/Intensity';
import { Exercise, Workout } from '../Logic/Workout';

class WorkoutCreatorBase{
    constructor(){
        this.validX = (Timer.stateData.Exercises.length!==0)?
                            Timer.stateData.Exercises:
                            Array.from({length: Timer.jsonData.images.wlen}, ()=>null).map((e,i)=>i);

        this.validY = (Timer.stateData.Levels.length!==0)?
                            Timer.stateData.Levels:
                            Array.from({length: Timer.jsonData.images.hlen}, ()=>null).map((e,i)=>i);

        this.intensity = Intensity()[Timer.stateData.Intensity];
        this.workout = new Workout();
    }

    getRndX(){
        return this.validX[Math.floor(Math.random()*this.validX.length)];
    }

    getRndY(){
        return this.validY[Math.floor(Math.random()*this.validY.length)];
    }

    create(){}
}

class SuspensionWorkout extends WorkoutCreatorBase{
    create(){
        this._createSets(3);
        this._popLastElement();
        this._changeNullPictures();
        Timer.workoutData = this.workout;
    }

    _createSets(reps){
        for (let i = 0; i < this.intensity.sets; i++) {
            var actSet = this._createSet();
            for (let j = 0; j < reps; j++) {
                this._concatActSet(actSet);
            }
        }
    }

    _popLastElement(){
        this.workout.pop();
    }

    _changeNullPictures(){
        for (let i = 0; i < this.workout.length(); i++) {
            const element = this.workout.get(i);
            if(!element.picture)
                element.picture = this.workout.get(i+1).picture;
        }
    }

    _concatActSet(actSet){
        for (let v = 0; v < actSet.length; v++) {
            this.workout.push(actSet[v]);
            if(v < actSet.length-1)
                this.workout.push(new Exercise('green', this.intensity.pauseTime, actSet[v+1].picture));
            else
                this.workout.push(new Exercise('green', this.intensity.pauseTime, null));
        }
    }

    _createSet(){
        var actSet = [];
        while(actSet.length < this.intensity.exer){
            var tX = this.getRndX();
            var tY = this.getRndY();
            var pictureName = tY+'_'+tX+'.jpg';
            if(Timer.jsonData.images.exercises[tX][tY] && this._hasSame(actSet, pictureName)){
                var tempExercises = new Exercise('orange', this.intensity.workTime, pictureName);
                actSet.push(tempExercises);
                if(Timer.jsonData.images.repeatedExercises.indexOf(pictureName) >= 0){
                    actSet.push(tempExercises);
                }
            }
        }
        return actSet;
    }

    _hasSame(actSet, picture){
        for (const iterator of actSet) {
            if(iterator.picture === picture)
                return false;
        }
        return true;
    }

}

class DumbbellWorkout extends WorkoutCreatorBase{
    create(){
        
    }
}

function createWorkout(){
    if(Timer.rootPath === 'dumbbell_exercises'){
        (new DumbbellWorkout()).create();
    }else if(Timer.rootPath === 'suspension_exercises'){
        (new SuspensionWorkout()).create();
    }
}

export default createWorkout;


