
function Intensity(){
    return Object.freeze({
      "low":{workTime: 45, pauseTime: 25, exer: 2, sets: 4, name:"Low (45s/25s)"},
      "high":{workTime: 30, pauseTime: 10, exer: 3, sets: 3, name:"High (30s/10s)"},
      "strength":{workTime: 90, pauseTime: 60, repeat: 3, sets: 5, name:"Strength (90s/60s)"},
      "endurance":{workTime: 60, pauseTime: 45, repeat: 3, sets: 5, name:"Endurance (60s/45s)"}
    });
  }
  
  function IntensityForInputs(filter=[]){
    var iteratables = [];
    for (const key in Intensity()) {
      if(filter.length !== 0 && !filter.includes(key))
        continue;
  
      var name = Intensity()[key].name;
      iteratables.push([
        name, key
      ]);
    }
    return iteratables;
  }
  
  function IntensitySelector(rootPath){
    if(rootPath === 'dumbbell_exercises'){
      return IntensityForInputs(['strength', 'endurance']);
    }else if(rootPath === 'suspension_exercises'){
      return IntensityForInputs(['low', 'high']);
    }
  }

  export { Intensity, IntensitySelector };


