import React, { Component } from 'react';
import './WorkoutInputs.css';

import { jsonContext } from '../Containers/Body';
import { IntensitySelector } from '../Logic/Intensity';

class WorkoutInputs extends Component {
  render() {
    return(
      <jsonContext.Consumer>
        {json=>{
          return(
            <div className="WorkoutInputs">
              <div className="row show-inputs mt-1">
                <div className="col-sm-12 col-md-4">
                  {this.getList('Exercises', json.inputs.Exercises, 'checkbox')}
                </div>
                <div className="col-sm-12 col-md-4">
                  {this.getList('Levels', json.inputs.Levels, 'checkbox')}
                </div>
                <div className="col-sm-12 col-md-4">
                  {this.getList('Intensity', IntensitySelector(json.rootPath), 'radio')}
                </div>
              </div>
              <div className="row">
                <button 
                  type="button" 
                  className="btn btn-primary btn-lg btn-block" 
                  onClick={this.props.createButtonFn}
                >Create Random Exercise</button>
              </div>
            </div>
          );
        }}
      </jsonContext.Consumer>
    );
  }

  getList(name, object, type){
    var liArr = []
    for (const lis of object) {
      var par = {
        type: type,
        group: name,
        value: lis.slice(1).join(',')
      };
      liArr.push(
        <li className="list-group-item" key={lis[0]}>{lis[0]}{this.getInput(par)}</li>
      );
    }
    return(
      <ul className="list-group">
        <li className="list-group-item disabled">{name}</li>
        {liArr}
      </ul>
    );
  }

  getInput(par){
    return(
      <label className="switch">
        <input 
          type={par.type} 
          name={par.group} 
          value={par.value} 
          className="primary"
          onChange={this.props.checkBoxFn}
        />
        <span className="slider round"></span>
      </label>
    );
  }
}

export default WorkoutInputs;



