import React, { Component } from 'react';
import './TimerDisplay.css';

class TimerDisplay extends Component {
    constructor(props){
        super(props);
        this.state = {
            onComplete: this.props.onComplete,
            remainingSec: this.props.remainingSec,
            actualSec: 0,
            hideSecDisplay: false
        };
        this.bar = React.createRef();
    }

  componentDidMount(){
    this._start();
  }
  
  _start(){
    this._stop();
    this._settingBar();
    Window.timerInterval = setInterval(()=>{
        this._countDown();
    }, 1000);
  }

  _stop(){
    if(Window.timerInterval)
        clearInterval(Window.timerInterval);
  }

  _countDown(){
    var newActualSec;
    this.setState((oldState)=>{
        newActualSec = oldState.actualSec + 1;
        return {actualSec: newActualSec};
    });

    this._settingBar();

    if(newActualSec >= this.state.remainingSec){
        this._stop();
        this.setState({hideSecDisplay: true})
        setTimeout(()=>{
            this.setState({actualSec: 0});
            this._settingBar();
            setTimeout(this.state.onComplete, 500);
        }, 500);
    }
  }

  _calculatePrecentage(){
    return (this.state.actualSec/this.state.remainingSec) * 100;
  }

  _settingBar(){
      try {
        var val = this._calculatePrecentage();
        if (isNaN(val)) {
            val = 100; 
        }
        else{
            var r = this.bar.current.attributes[1].value;
            var c = Math.PI*(r*2);

            if (val < 0) { val = 0;}
            if (val > 100) { val = 100;}
            var pct = ((100-val)/100)*c;

            this.bar.current.style.strokeDashoffset = pct;
        }   
      } catch (error) {
        this._stop();      
      }
  }

  _calculatePPTime(){
      var remaining = this.state.remainingSec - this.state.actualSec;
      return ('0'+parseInt(remaining/60, 10)).slice(-2) + ':' + ('0'+parseInt(remaining%60, 10)).slice(-2);
  }

  render() {
    return(
        <div className="TimerDisplay">

            <div id="cont" className={this.state.hideSecDisplay?"hide":''} data-pct={this._calculatePPTime()}>
                <svg id="svg" width="80%" height="100%" viewBox="0 0 200 200">
                    <circle r="90" cx="100" cy="100" fill="transparent" strokeDasharray="565.48" strokeDashoffset="0"></circle>
                    <circle ref={this.bar} id={this.props.color+"-bar"} r="90" cx="100" cy="100" fill="transparent" strokeDasharray="565.48" strokeDashoffset="100"></circle>
                </svg>
            </div>

        </div>
    );
  }
}

export default TimerDisplay;



