var ding1 = new Audio(process.env.PUBLIC_URL + '/ding.mp3');
var ding2 = new Audio(process.env.PUBLIC_URL + '/ding.mp3');

function singleDing(){
    ding1.play();
}

function doubleDing(){
    singleDing();
    setTimeout(()=>{
        ding2.play();
    }, 400);
}

export {
    singleDing,
    doubleDing
};


