import React, { Component } from 'react';
import './Timer.css';

import ExerciseFrame from '../Components/ExerciseFrame';
import ProgressBar from '../Components/ProgressBar';

import { jsonContext } from '../Containers/Body'

import { singleDing, doubleDing } from '../Components/Dinger';

class Timer extends Component {
  constructor(props){
    super(props);
    this.state = {
      actExercise: Timer.workoutData.getAct(),
      actEx: Timer.workoutData.getPrecentage() 
    };
    this.next = this.next.bind(this);
    this.prev = this.prev.bind(this);
  }

  render() {
    if(this.state.actExercise){
      return this._getTimerView();
    }else{
      return this._getFinishView();
    }
  }

  next(){
    this.setState({
      actExercise: Timer.workoutData.next()
    });
    this.updatePrecentage();
  }

  prev(){
    this.setState({
      actExercise: Timer.workoutData.prev()
    });
    this.updatePrecentage();
  }

  updatePrecentage(){
    this.setState({actEx: Timer.workoutData.actEx});
  }

  _getNextSign(){
    var timerColorIsGreen = this.state.actExercise.color !== 'green';
    var nextClasses = "next-sign display-4" + (timerColorIsGreen?' transition':'');
    return(
      <div className={nextClasses}>Next</div>
    );
  }

  _dinging(){
    if(this.state.actExercise.color === 'green')
      singleDing();
    else
      doubleDing();
  }

  _getTimerView(){
    return(
        <div>
          <div className="row mt-2">
            <div className="col-sm-12 col-md-6 text-center">
              {this.state.actExercise.getTimer(()=>{
                this._dinging();
                this.next();
              })}
            </div>
            <div className="col-sm-12 col-md-6 text-center d-flex align-items-center">
            <jsonContext.Consumer>
                {json=>{
                  return(
                    <div className="timer-ex-frame">
                      {this._getNextSign()}
                      <ExerciseFrame 
                        rootPath={json.rootPath}
                        filename={this.state.actExercise.picture}
                      />
                    </div>  
                  );
                }}
              </jsonContext.Consumer>
            </div>
          </div>
          <ProgressBar 
            actEx={this.state.actEx}
          />
          <div className="btn btn-light stepper-button prev" onClick={this.prev}>&lt;</div>
          <div className="btn btn-light stepper-button next" onClick={this.next}>&gt;</div>
        </div>
    );
  }

  _getFinishView(){
    return(
      <div class="display-4 mt-5 text-center">Congratulation!</div>
    );
  }
}
Timer.rootPath = undefined;
Timer.stateData = undefined;
Timer.jsonData = undefined;
Timer.workoutData = undefined;

export default Timer;



