
import React, { Component } from 'react';
import './ProgressBar.css';

import Timer from '../Pages/Timer';

class ProgressBar extends Component {
    constructor(props){
        super(props);
        this.precentages = [];
        for (const iterator of Timer.workoutData.exList) {
            var act = ((iterator.remainingSec / Timer.workoutData.sumTime) * 100).toFixed(4);
            this.precentages.push(
                [act, iterator.color]
            );
        }
    }

    render(){
        return(
            <div className="bottom-progress-bar p-2">
                <div className="progress">
                    {this._getProgressBars()}
                </div>
            </div>
        );
    }

    _getProgressBars(){
        var bars = [];
        for (let i = 0; i < this.precentages.length; i++) {
            const specs = this.precentages[i];
            bars.push(
                this._getProgressBar(i, specs)
            );
        }
        return bars;
    }

    _getProgressBar(index, specs){
        var classes = "progress-bar " + 
                        specs[1] + 
                        (index > this.props.actEx?
                            " transparent":
                            (index === this.props.actEx?
                                " progress-bar-animated progress-bar-striped":
                                ""
                            )
                        ); 
        return(
            <div 
                className={classes}
                role="progressbar" 
                style={{width: specs[0]+'%'}} 
                key={index}
            ></div>
        );
    }
}

export default ProgressBar;


