import React from 'react';

function modalBase(props){
    return (<div className="modal fade" id={props.id} tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog" role="document">
            <div className="modal-content">
                <div className="modal-header">
                    <h5 className="modal-title">{props.title}</h5>
                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div className="modal-body">
                    {props.body}
                </div>
            </div>
        </div>
    </div>);
}

const intensityWarningModal = modalBase({
    "id": 'intensityWarningModal',
    "title": 'Warning!',
    "body": 'Please select Intensity.'
});

const creatingWorkoutModal = modalBase({
    "id": 'creatingWorkoutModal',
    "title": 'Info!',
    "body": 'Creating random workout. Please wait...'
});

export {
    intensityWarningModal,
    creatingWorkoutModal
};


