
import React from 'react';
import TimerDisplay from '../Components/TimerDisplay';

class Exercise{
    constructor(color, remainingSec, picture){
      this.picture = picture;
      this.color = color;
      this.remainingSec = remainingSec;
    }

    getTimer(fn){
      return(
        <TimerDisplay
          onComplete={fn}
          remainingSec={this.remainingSec}
          color={this.color}
          key={this.picture+'_'+this.color}
        />
      );
    }
  }
  
  class Workout{
    constructor(){
      var warmupLength = 60;

      this.actEx = 0;
      this.exList = [ new Exercise('green', warmupLength, null) ];
      this.sumTime = warmupLength;
    }

    get(index){
      return this.exList[index];
    }

    length(){
      return this.exList.length;
    }

    push(exer){
      this.sumTime += exer.remainingSec;
      this.exList.push(exer);
    }

    pop(){
      this.sumTime -= this.exList.pop().remainingSec;
    }

    getPrecentage(){
      return parseInt((this.actEx/this.exList.length)*100, 10);
    }
  
    getAct(){
        return this.exList[this.actEx];
    }
  
    next(){
      if(this.exList.length > this.actEx+1){
        this.actEx++;
        return this.getAct();
      }else{
        return null;
      }
    }
  
    prev(){
      if(this.actEx > 0)
      this.actEx--;
      return this.getAct();
    }
  }

export { Exercise, Workout };


